<template>
  <div class="likes">
      <button @click="unlikes">
        <font-awesome-icon :icon="['fas','heart']" v-if="likes" class="red"/>
        <font-awesome-icon :icon="['far','heart']" v-if="!likes"/>
      </button>
  </div>
</template>

<script>
export default {
    data() {
        return {
            likes: false
        }
    },

    methods: {
        unlikes(){
            if(this.likes == true){
                this.likes = false
            }else{
                this.likes = true
            }
        },
    },

}
</script>

<style lang="scss">
@import '@/assets/css/common/common.scss'
</style>